import Swiper from 'swiper';
import { Navigation, Pagination } from 'swiper/modules';

Swiper.use( [Navigation, Pagination] );

// -----------------------------------
// --- SAME PRODUCT SECTION SLIDER ---
// -----------------------------------

const swiper_same = new Swiper('.swiper-same', {
  slidesPerView: 2,
  slidesPerGroup: 1,
  spaceBetween: 16,
  speed: 800,
  watchOverflow: true,
  navigation: {
    nextEl: '.same-btn-next',
    prevEl: '.same-btn-prev',
  },
  keyboard: {
    enabled: true,
    onlyInViewport: false,
    pageUpDown: true,
  },
  breakpoints: {
    576: {
      spaceBetween: 32,
    },
    1000: {
      slidesPerView: 3,
      spaceBetween: 32,
    },
    1310: {
      slidesPerView: 4,
      spaceBetween: 32,
    },
  },
});
